.offcan-box{
    position: absolute;
    width: 230px;
    height: 90vh;
    display: none;
    z-index: 10;
  
    overflow-y: auto; 
    scrollbar-color: #d4aa70 #e4e4e4;
    scrollbar-width: thin;
}

.offcan-box::-webkit-scrollbar {
    width: 2px;
}
.offcan-box::-webkit-scrollbar-track {
    background: var(--color2);
    /* border-radius: 100px; */
}
.offcan-box::-webkit-scrollbar-thumb {
    border-radius: 100px;
    background-image: linear-gradient(180deg, rgb(141, 141, 141) 0%, rgb(122, 122, 122) 99%);
    box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
}
.users-list{
    margin-top: 15px;
}
.users-list> li > .users-link{
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    transition: .5s;
}
.users-list > li > .users-link > p{
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #898989;
}
.users-list > li > .users-link > p:hover{
    color: red;
 }
.user-img{
    border-radius: 50%;
    margin-right: 10px;
}
.sub-box{
    margin-top: 10px;
    padding-left: 20px;
    border: none;
    width: 100%;
    background: var(--color2) ;
}
dialog{
    position: none;
}
.shows-link{
    margin-top: 7px;
    border: none;
    outline: none;
    background: none;
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #898989;
    cursor: pointer;
    transition: .5s;
}
.shows-link > i{
    font-size: 23px;
    margin-right: 7px;
}
.shows-link:focus{
    color: red;
}
.shows-link:hover{
    color: red;
}
.sub-box > h3{
    font-weight: bold;
    font-size: 18px;
    line-height: 23px;
    color: var(--color);
}
.setting-box{
    margin-top: 89px;
    margin-bottom: 30px;
}
.set-link{
    margin-bottom: -25px;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    color: #C2C2C2;
    display: flex;
    align-items: center;
    
}
.set-link:focus{
    color: red;
}
.set-link:hover{
    color: red;
}
.set-link > i{
    font-size: 30px;
    margin-right: 10px;
}
.offcan{
    position: fixed;
    margin-top: 65px;
    z-index: 20;
}
.offcanvas-content{
    padding: 20px;
    background: var(--color2) ;
}
#off-btn{
    position: absolute;
    top: 10px;
    right: 10px;
    border: none;
    background: none;
    color: #898989;
    font-size: 30px;
}
.link-item{
    margin-bottom: 14px;
}
.item-link{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #898989;
}
.item-link:focus{
    color: red;
}
.item-link:hover{
    color: red;
}
.link-item:nth-child(3){
    margin-bottom: 40px;
}
.item-link > .bx{
    font-size: 23px;
    margin-right: 7px;
}
.tem-link-active{
    color: red;
}
.sidebar-box{
    position: fixed;
    height: 100vh;
    width: 75px;
    background: var(--color2);
  
    /* background: #F2F2F2; */
}
.sidebar-content{
    margin-top: 90px;
    background: var(--color2);

}
.sidebar-content > ul {
    padding: 0 10px;

}
.sidebar-content > ul > li {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 30px;
    padding: 0 10px;
    text-align: center;
 
}
.sidebar-content > ul > li:hover .side-link>span{
   color: red;
}
.sidebar-content > ul > li:hover .side-link>i{
    color: red;
 }
 .sidebar-content > ul > li:focus .side-link>span{
    color: red;
 }
 .sidebar-content > ul > li:focus .side-link>i{
     color: red;
  }
.sidebar-content > ul > li > .side-link > span{
    color: var(--color);
    font-size: 11px;
    font-weight: bold;
    opacity: 0.6;
}
.sidebar-content > ul > li > .side-link > i{
    color: var(--color);
    opacity: 0.6;
    font-size: 25px;
    font-weight: 600;
}