.video{
    min-height: 100vh;
    padding-top: 70px;
    padding-left: 80px;
}
h1{
    color: var(--color);
}.wrapper{
    display: flex;
    align-items: top;
    justify-content: space-between;
}
.video-big-box{
    display: flex;
    align-items: start;
    flex-direction: column;
    width: 65%;
    border-bottom: none;
    padding: 25px;
}
.video-small-box{
    width: 35%;
    padding: 10px;
}
.big-video{
    width: 99%;
    height: 400px;
    border-radius: 12px;
    background: #999;
}
.big-video .videos-box{
    width: 100%;
    border-radius: 12px;
}
.videos-box iframe{
    width: 100%;
    height: 400px;
    border-radius: 12px;
}
.btn-box{
    display: flex;
    align-items: center;
    justify-content: end;
    margin-left: auto;
    margin-right: 14px;
    margin-top: 30px;
    margin-bottom: 10px;
}
.similar-btn{
    
    display: flex;
    align-items: center;
    margin-right: 10px;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    padding: 7px 13px;
    color: #e6e3e3;
    background: #2E2E34;
    border: none;
    border-radius: 20px;
    outline: none;
    cursor: pointer;
}
.similar-btn > i{
    font-size: 20px;
    margin-right: 7px;
    color: #828285;
    mix-blend-mode: normal;
    opacity: 0.6;
}
.btn{
    border: none;
    outline: none;
    background: none;
    font-size: 30px;
    cursor: pointer;
    color: var(--color);
}
.div{
    width: 100%;
    border-top: 2px solid #828285;
}
.video-list  iframe{
    width: 96%;
    height: 225px;
    border-radius: 13px;
}
.video-list{
    margin-left: 10px;
    margin-top: 20px;
}
.video-list > li{
    margin-bottom: 20px;
}
.auto-box{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.div-title{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.auto-box > h2{
    font-weight: bold;
    font-size: 36px;
    line-height: 45px;
    color: var(--color)
}
.food{
    font-weight: bold;
    font-size: 36px;
    line-height: 45px;
    color: var(--color);
}
.subs-btn{
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    background: #FF0000;
    border-radius: 20px;
    outline: none;
    border: 2px solid transparent;
    padding: 10px 20px;
    cursor: pointer;
    transition: .5s;
}
.subs-btn:hover{
    color: #FF0000;
    border: 2px solid #FF0000;
    background: transparent;
}
.boxx{
    display: flex;
    align-items: center;
}
.boxx > p{
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--color);
}
/* toggle start */
.switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 23px;
    margin-left: 10px;
  }
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 2px;
    bottom: 2px;
    background-color: rgb(74, 14, 240);
    -webkit-transition: .4s;
    transition: .4s;
  }
  input:checked + .slider {
    background-color: #0f0;
  }
  input:checked + .slider:before{
      background: #fff;
  }
  input:focus + .slider {
    box-shadow: 0 0 1px #0f0;
  }
  input:checked + .slider:before {
    -webkit-transform: translateX(16px);
    -ms-transform: translateX(16px);
    transform: translateX(16px);
  }
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  .slider.round:before {
    border-radius: 50%;
  }
  /* toggle end */
  .food-box{
