*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  overflow-y: scroll;
  overflow-x: hidden;
  background: #F2F2F2;
  scrollbar-color: #d4aa70 #e4e4e4;
  scrollbar-width: thin;
}
body::-webkit-scrollbar {
width: 10px;
}
body::-webkit-scrollbar-track {
background-color: #e4e4e4;
border-radius: 100px;
}
body::-webkit-scrollbar-thumb {
border-radius: 100px;
background-image: linear-gradient(180deg, rgb(105, 105, 105) 0%, rgb(80, 79, 79) 99%);
box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
}
ol, ul{
  list-style: none;
}
a{
  text-decoration: none;
  color: var(--color);
}
.containers{
  width: 1315px;
  margin: 0 auto;
}
.container{
  width: 1265px;
  margin: 0 auto;
}

.dark{
  background: #020A18;
  --color2:#010e25;
  --color: #fff;
  transition: 1s ease-in-out;
  --shadow: 0 5px 10px rgba(75, 73, 73, 0.575);
  --input:#EBEBEB;
}

.light{
  background: #F2F2F2;
  color: #02070f;
  --color: #02070f;
  --color2:#ececec;
  transition: 1s ease-in-out;
  --shadow: 0 5px 10px rgb(182, 182, 182);
}