.nav-box{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
header{
    padding: 10px 0;
    position: fixed;
    width: 100%;
    z-index: 20;
    background: var(--color2) !important;
    /* box-shadow: var(--shadow); */
}
.nav-small-box{
    display: flex;
    align-items: center;
}
.toggle{
    border: none;
    outline: none;
    background: none;
    font-size: 35px;
    font-weight: bold;
    margin-right: 15px;
    cursor: pointer;
    margin-top: 6px;
    color: var(--color);
}

.nav-title{
    display: flex;
    align-items: center;
    margin-right: 50px;
    cursor: pointer;
    
}
.nav-title > i{
    color: red;
    font-size: 40px;
}
.nav-title > span{
    color: var(--color);
    font-size: 30px;
}
.input-box{
    width: 510px;
    position: relative;
    
}
.input-box > input{
    width: 100%;
    outline: none;
    border-radius: 50px;
    padding: 10px 20px;
    background: #ccc;
    font-weight: bold;
    border: none;
}
.input-icon{
    position: absolute;
    right: 12px;
    top: 7px;
    font-size: 20px;
    font-weight: bold;
}
.nav-icons-box{
    display: flex;
    align-items: center;
}
.nav-icons-box > button{
    outline: none;
    border: none;
    background: none;
    font-size: 27px;
    margin-right: 20px;
    color: var(--color);
    opacity: 0.5;
    cursor: pointer;
}
.theme-select{
    border: none;
    font-size: 25px;
    outline: none;
    background: none;
    margin-right: 20px;
    color: var(--color);
}