.channel{
    min-height: 100vh;
    padding-top: 80px;
    padding-left: 80px;
}
.ch2{
    color: var(--color);
    margin-left: 200px;
    margin-top: 300px;
}
.acc-big-box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
}
.acc-title-box{
    display: flex;
    align-items: center;
}
.acc-title-box > img{
    margin-top: 5px;
}
.title-info-box > h3{
    padding: 0;
    line-height: 0;
    font-weight: bold;
    font-size: 26px;
    color: var(--color);
}
.subs-span{
    font-weight: 500;
    font-size: 15px;
    line-height: 16px;
    color: #b1aeae;
}
.title-info-box{
    margin-left: 15px;
}
.acc-small-box{
    display: flex;
    align-items: center;
}
.acc-span{
    color: #8f8e8e;
    mix-blend-mode: normal;
    cursor: pointer;
    font-size: 30px;
    margin-right: 25px;
    cursor: pointer;
}
.tab-list{
    margin-top: 20px;
    display: flex;
    align-items: center;
}
.tab-list > li {
    margin-right: 50px;
}
.tab-link{
    font-weight: bold;
    font-size: 18px;
    line-height: 23px;
    color: var(--color);
    border-bottom: 2px solid transparent;
    transition: .5s;
    cursor: pointer;
}
.tab-link:hover{
    color: #f00;
}
.tab-link:focus{
    color: #f00;
    border-bottom: 2px solid #f00;
}
.tab{
    font-size: 22px;
    margin-top: 5px;
    font-weight: bold;
}
.video-divs > .videoss-link{
    margin-top: 40px;
    display: flex;
    align-items: center;
}
.videoss-link{
    display:block;
}
.avatar{
    border-radius: 50%;
}
.iframe-box{
    margin-right: 25px;
}
.iframe-box iframe{
    border-radius: 15px;
}
.video-info-box {
    display: inline-block;
}
.video-info-box > h3{
    font-weight: bold;
    font-size: 26px;
    line-height: 34px;
    margin: 0;
    color: var(--color);
}
.video-info-box > p{
    font-weight: 500;
    margin: 15px 0;
    font-size: 16px;
    line-height: 22px;
    color: var(--color);
    mix-blend-mode: normal;
    opacity: 0.6;
}
.video-span{
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #C2C2C2;
}
.channel-box{
    margin-left: 70px;
    margin-top: -20px;
}
.channel-box > .channel-span{
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #C2C2C2;
}
.chan-list > li{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.chan-list > li:nth-child(1){
    margin-top: 35px;
}
.chan-list > li > img{
    margin-right: 10px;
}
.chan-list > li > h4{
    font-weight: bold;
    font-size: 18px;
    line-height: 23px;
    color: var(--color);
}