.home{
    padding-top: 60px ;
    padding-left: 80px;
    min-height: 100vh;
}
.home-box{
    padding: 20px 0;
}
.list2, .list3{
    display: flex;
    align-items: top;
    padding: 20px 0;
}
.list{
    display: flex;
    align-items: top;
    padding: 20px 0;

}
.list, .list2, .list3{
    overflow-x: auto;
    scrollbar-color: #d4aa70 #e4e4e4;
    scrollbar-width: thin;
}
.list::-webkit-scrollbar {
 height: 3px;
}
.list::-webkit-scrollbar-track {
background-color: #e4e4e4;
border-radius: 100px;
}
.list::-webkit-scrollbar-thumb {
border-radius: 100px;
background-image: linear-gradient(180deg, rgb(105, 105, 105) 0%, rgb(80, 79, 79) 99%);
box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
}
.list2::-webkit-scrollbar {
    height: 3px;
    }
    .list2::-webkit-scrollbar-track {
    background-color: #e4e4e4;
    border-radius: 100px;
    }
    .list2::-webkit-scrollbar-thumb {
    border-radius: 100px;
    background-image: linear-gradient(180deg, rgb(105, 105, 105) 0%, rgb(80, 79, 79) 99%);
    box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
    }
    .list3::-webkit-scrollbar {
        height: 3px;
        }
        .list3::-webkit-scrollbar-track {
        background-color: #e4e4e4;
        border-radius: 100px;
        }
        .list3::-webkit-scrollbar-thumb {
